* {
    border-radius: 0 !important;
    font-size: 18px;
    box-sizing: border-box;
  }
  
  h1 {
    margin-top: 20px;
  }
  
  .container {
    text-align: center;
    width: 400px;
    margin-top: 30px;
    padding: 15px;
    box-shadow: 0px 0px 10px 1px rgba(0, 0, 0, 0.35);
    background: #a1a1a1;
  }
  
  a:active, a:hover {
    outline: 0;
  }
  
  a {
    background-color: #ccc;
    font-weight: 700;
    color: black;
    height: 60px;
    width: 24%;
    margin-top: 5px;
    text-decoration: none;
  }
  
  .keys {
    margin-bottom: 10px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  
  .keys .display {
    height: 120px;
    width: 100%;
    margin-bottom: 10px;
    background: #ffc600;
    text-align: right;
    font-size: 50px;
    padding-top: 30px;
    padding-right: 10px;
    border: 1px solid #ccc;
  }
  
  .digit {
    background: #f7f7f7;
    font-size: 22px;
  }
  
  .btn:hover {
    background: #4D4D50;
    color: #fff;
    border: 1px solid #ffc600;
  }
  
  .operator:hover {
    background: #ffc600;
    border: 1px solid #4D4D50;
  }
  
  div .github {
    background: none;
    text-decoration: none;
  }
  
  div .github:hover {
    color: #ffc600;
    transition: .2s;
  }